<template>
    <div>
      <div class="head">
        <div @click="select('1')" :class="{ selected : tag==='1'}">
          <span>我的订单</span>
        </div>
         <!-- <div @click="select('2')" :class="{ selected : tag==='2'}">
          <span>待就诊</span>
        </div>
        <div @click="select('3')" :class="{ selected : tag==='3'}">
          <span>已就诊</span>
        </div>
        <div @click="select('4')" :class="{ selected : tag==='4'}">
          <span>已取消</span>
        </div> -->
      </div>
      <order-card v-for="(item, index) in fliterList" :key="index" :status="item.status" :dateStr="item.dateString" :name="item.name" :telphone="item.telphone">

      </order-card>
      <div style="height: 20px"></div>
    </div>
</template>

<script>

import orderCard from 'components/orderCard/orderCard'
import {querySubListByUser} from '@/api'
export default {
  mounted () {
      var _this = this
      var params = new URLSearchParams()
      querySubListByUser(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          _this.fliterList = data.data
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
  },
  data () {
    return {
      tag: '1',
      fliterList: []
    }
  },
  components: {
    orderCard
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

.head
    @extend .flex
    @extend .flex-row
    @extend .flex-around
    height 60px
    background-color white
    color black

.selected
    color white
    background-color $color-theme-blue
    height 30px
    border-radius 15px
    span
        line-height 30px
        padding 10px
</style>


